<template>
	<div class="page-wrapper">
		<div class="page-breadcrumb">
			<div class="row">
				<div class="col-12 d-flex no-block align-items-center">
					<h4 class="page-title" v-text="noticeText" />
				</div>
			</div>
		</div>
	</div>
</template>

<script>

export default {
    name: 'failure',
    data() {
        return {
		   ticker: null,
		   time: 5,
        }
    },
    components: {
    },
    methods: {
		
	},
	beforeDestroy() {
		clearInterval(this.ticker)
	},
    created() {},
	mounted() {
		this.ticker = setInterval(() => {
			if (--this.time <= 0) {
				this.$router.back()
			}
		}, 1000);
	},
    computed: {
		noticeText() {
			return `頁面錯誤，將在 ${this.time} 秒後自動返回上一頁`
		}
    },
}

</script>

